.background {
  background-image: linear-gradient(#667db6, #0082c8, #0082c8, #667db6);
  background-size: cover;
  height: 100%;
  width: 100%;
  align-content: center;
  position: fixed;
  z-index: -1;
  overflow: hidden;
}

.background canvas {
  background-size: cover;
  height: 100%;
  width: 100%;
  position: fixed;
  overflow: hidden;
}
